import React, {Component} from 'react'
import IsPcOrNot from './IsPcOrNot'
import en from '../../locale/en';
import zh from '../../locale/zh';
import Constant from "./Constant";
import {Menu, FormattedMessage} from './BaseComponent'

let messages = {};
messages['en'] = en;
messages['zh-CN'] = zh;
messages['zh-cn'] = zh;

class SelectLine extends Component {
    constructor(props) {
        super(props);
        this.error = {
            text: "sure"
        }
        this.flag = false;
        this.submitFlag = true;
    }

    handleInputChange = (key, val) => {
        this.props.form[key] = val;
        if (key === 'whetherUseAi' || key === 'whetherUseBaiduAi') {
            // console.log(this.props.appear)
            this.props.refresh();
        }
        this.submitFlag = true;
        this.setState({
            submitFlag: this.submitFlag
        })
        this.setState(this.props.form);
    }

    refresh = () => {
        // this.inputValue = this.props.form[this.props.name];
        // this.setState({
        //     inputValue:this.inputValue
        // })
    }

    show = (e) => {
        // console.log(e.currentTarget)
        if (e.target == e.currentTarget) {
            this.flag = !this.flag;
            this.setState({
                flag: this.flag
            })
        } else {
            return
        }

    }

    componentDidMount() {
        this.valid = () => {
            this.submitFlag = true;
            if (this.props.valid !== undefined) {
                let valids = this.props.valid.split("||");
                for (let i = 0; i < valids.length; i++) {
                    if (valids[i] === 'notNull') {
                        if (this.props.form[this.props.name] === undefined || this.props.form[this.props.name].length === 0 || this.props.form[this.props.name] === '') {
                            this.error.text = "every_thing_is_notnull";
                            this.setState(this.error);
                            this.refs.Menu.open();
                            setTimeout(() => {
                                this.refs.checkItem.scrollIntoView();
                            }, 300);
                            this.submitFlag = false;
                            this.setState({
                                submitFlag: this.submitFlag
                            })
                            break;
                        }
                    }
                }
                return this.submitFlag;
            }
        }
    }

    render() {
        return (
            <div ref="checkItem">
                <Menu ref="Menu" text={this.error.text}/>
                <div style={{margin: "0 0 25px 0"}}>
                    <div style={{
                        width: IsPcOrNot.get() ? '600px' : '5.26rem',
                        height: "auto",
                        border: this.submitFlag ? '1px solid #000' : '1px solid red',
                        boxSizing: 'border-box',
                        borderRadius: '2px',
                        textAlign: 'left'
                    }}>
                        <div defaultValue="default" value={this.props.form[this.props.name]} name={this.props.name}
                             style={{
                                 width: '100%',
                                 borderBottom: this.submitFlag ? '1px solid #000' : '1px solid red',
                                 boxSizing: 'border-box',
                                 height: "40px",
                                 display: 'flex',
                                 alignItems: "center",
                                 flexWrap: 'wrap',
                                 fontSize: "14px",
                                 background: "#fff",
                                 paddingLeft: "10px",
                                 color: '#000',
                             }}>
                            <div value='default'>{messages[localStorage['language']][this.props.text]}</div>
                        </div>
                        {
                            this.props.data.map((item, key) => {
                                return (
                                    <div style={{
                                        height: '40px',
                                        lineHeight: '40px',
                                        paddingLeft: "10px",
                                        cursor: 'pointer',
                                        backgroundColor: (this.props.form[this.props.name] == item.value) ? '#cce9ff' : ''
                                    }}
                                         key={key}
                                         onClick={this.handleInputChange.bind(this, this.props.name, item.value)}
                                         value={item.value}>{item.text}
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
            </div>
        )
    }
}

class CheckBoxLine extends Component {
    constructor(props) {
        super(props);
        this.error = {
            text: "sure"
        }
        this.flag = false;
        this.submitFlag = true;
        this.checked = false;
    }

    componentDidMount() {

    }

    show = (event) => {
        this.flag = !this.flag;
        this.setState({
            flag: this.flag
        })
    };

    valid = () => {
        this.submitFlag = true;
        if (this.props.valid != undefined) {
            let valids = this.props.valid.split("||");
            for (let i = 0; i < valids.length; i++) {
                if (valids[i] === 'notNull') {
                    if (this.props.form[this.props.name] === undefined || this.props.form[this.props.name].length === 0 || this.props.form[this.props.name] === '') {
                        setTimeout(() => {
                            this.refs.checkBox.scrollIntoView();
                        }, 300);
                        this.error.text = "every_thing_is_notnull";
                        this.setState(this.error);
                        this.refs.Menu.open();
                        this.submitFlag = false;
                        break;
                    }
                }
            }
        }
        return this.submitFlag;
    }

    chexbox = (key, event) => {
        this.submitFlag = true;
        if (event.target.checked == true) {
            this.handCheckBox(key, event.target.value, true);
        }
        if (event.target.checked == false) {
            this.handCheckBox(key, event.target.value, false);
        }
    };

    handCheckBox = (key, value, flag) => {
        if (this.props.form[key] == undefined || this.props.form[key] == '') {
            this.props.form[key] = "" + value;
        } else {
            if (flag) {
                this.props.form[key] = this.props.form[key] + ',' + value
            } else {
                // let str = this.props.form[key];
                // let index = str.indexOf(value);
                // if (index == 0) {
                //     this.props.form[key] = str.slice(index+2)
                // } else if (index == str.length - 1) {
                //     this.props.form[key] = str.slice(0, index - 1);
                // } else {
                //     this.props.form[key] = str.slice(0, index - 1) + str.slice(index + 1);
                // }

                this.props.form[key] = this.props.form[key].split(',');
                this.props.form[key].forEach((v, i) => {
                    if (v == value) {
                        this.props.form[key].splice(i, 1)
                    }
                })

                this.props.form[key] = this.props.form[key].join(',')
            }
        }
        this.setState(this.props.form);
    }
    renderChecked = (item,key) => {
        let arr = this.props.form[this.props.name];
        let checked = false;
        if (arr) {
            var brr = arr.split(',');
            for (var i = 0; i < brr.length; i++) {
                if (brr[i] == item.value) {
                    checked = true
                    break
                }
            }
        }
        return (
            <li className="dropdown-message-li" key={key} style={{
                marginBottom: "10px",
                width: "100%",
                textAlign: "left",
                listStyle: 'none'
            }}>
                <label style={{
                    display: "block",
                    cursor: "pointer"
                }} className="dropdown-message-label">
                    <input className="dropdown-message-input" type="checkbox"
                           onChange={this.chexbox.bind(this, this.props.name)}
                           checked={checked}
                           name={this.props.name} value={item.value}/>&nbsp;&nbsp;
                    <FormattedMessage id={item.text}/>
                </label>
            </li>
        )
    }

    render() {
        return (
            <div className="dropdown-checkbox" ref='checkBox' style={{
                border: this.submitFlag ? "0" : '1px solid red',
                boxSizing: 'border-box'
            }}>
                <Menu ref="Menu" text={this.error.text}/>
                <div className="dropdown-message" onClick={this.show} style={{
                    width: IsPcOrNot.get() ? "600px" : '5.26rem',
                    height: "45px",
                    borderRadius: "5px",
                    textAlign: "left",
                    display: 'flex',
                    flexWrap: 'wrap',
                    alignItems: "center",
                    fontSize: "14px",
                    userSelect: "none",
                    boxSizing: 'border-box',
                    color: '#000'
                }}>
                    {messages[localStorage['language']][this.props.text]}
                </div>
                <div className="dropdown-message-ul-wrapper" style={{width: IsPcOrNot.get() ? 'auto' : '5.26rem'}}>
                    <ul className="dropdown-message-ul" style={{
                        width: IsPcOrNot.get() ? "600px" : '5.26rem',
                        height: 'auto',
                        margin: "0",
                        padding: "5px 0",
                        background: "#fff",
                        border: "1px solid #rgb(68, 68, 68)",
                        boxSizing: 'border-box',
                        fontSize: "14px",
                        display: 'block'
                    }}>
                        {
                            this.props.data.map((i,key) => {
                                return (
                                    this.renderChecked(i,key)
                                )
                            })
                        }
                    </ul>
                </div>
            </div>
        )

    }
}

class InputLine extends Component {
    constructor(props) {
        super(props);
        this.error = {
            text: "sure"
        }
        this.submitFlag = true
    }

    handleInputChange = (key, event) => {
        this.submitFlag = true;
        this.setState({
            submitFlag: this.submitFlag
        })
        this.props.form[key] = event.target.value;
        this.setState(this.props.form);
    }

    componentDidMount() {
        this.valid = () => {
            this.submitFlag = true;
            if (this.props.valid != undefined) {
                let valids = this.props.valid.split("||");
                for (let i = 0; i < valids.length; i++) {
                    if (valids[i] === 'notNull') {
                        if (this.props.form[this.props.name] === undefined || this.props.form[this.props.name].length === 0 || this.props.form[this.props.name] === '') {
                            setTimeout(() => {
                                this.refs.checkItem.scrollIntoView();
                            }, 300);
                            this.error.text = "every_thing_is_notnull";
                            this.setState(this.error);
                            this.refs.Menu.open();
                            this.submitFlag = false;
                            break;
                        }
                    }
                    if (valids[i] == 'idCard') {
                        if (!(/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(this.props.form[this.props.name]))
                            && !(/^[a-zA-Z]{5,17}$/.test(this.props.form[this.props.name]))
                            && !(/^[a-zA-Z0-9]{5,17}$/.test(this.props.form[this.props.name]))
                            && !(/^[HMhm]{1}([0-9]{10}|[0-9]{8})$/.test(this.props.form[this.props.name]))
                            && !(/^[0-9]{8}$/.test(this.props.form[this.props.name]))
                            && !(/^[0-9]{10}$/.test(this.props.form[this.props.name]))
                        ) {
                            this.error.text = "idcard_is_wrong";
                            this.setState(this.error);
                            this.refs.Menu.open();
                            this.submitFlag = false;
                        }
                    }
                    if (valids[i] == 'email') {
                        if (!(/^([0-9A-Za-z\-_\.]+)@([0-9a-z]+\.[a-z]{2,3}(\.[a-z]{2})?)$/g).test(this.props.form[this.props.name])) {
                            this.error.text = "email_is_wrong";
                            this.setState(this.error);
                            this.refs.Menu.open();
                            this.submitFlag = false;
                        }
                    }
                }
            }
            this.setState({
                submitFlag: this.submitFlag
            })
            return this.submitFlag;
        }
    }

    render() {
        return IsPcOrNot.get() ? <div ref="checkItem" style={{margin: "0 0 25px 0"}}>
            <Menu ref="Menu" text={this.error.text}/>
            <div>
                <div style={{
                    backgroundColor: "#fff",
                    whiteSpace: "nowrap",
                    border: this.submitFlag ? "1px solid #000" : "1px solid red",
                    boxSizing: 'border-box',
                    overflow: "hidden",
                    borderRadius: "4px",
                    width: "600px",
                    height: "45px"
                }}>
                    <img style={{textAlign: "left", height: "24px", verticalAlign: "middle", marginLeft: "20px"}}
                         src={this.props.icon}/>
                    <input placeholder={messages[localStorage['language']][this.props.text]} style={{
                        paddingLeft: "10px",
                        width: "500px",
                        border: "0",
                        height: "100%",
                        fontSize: "12px",
                        color: '#000'
                    }} type="text" name={this.props.name} onChange={this.handleInputChange.bind(this, this.props.name)}
                           value={this.props.form[this.props.name]}/>
                </div>
            </div>
        </div> : <div ref='checkItem' style={{margin: "0 0 25px 0"}}>
            <Menu ref="Menu" text={this.error.text}/>
            <div>
                <div style={{
                    backgroundColor: "#fff",
                    whiteSpace: "nowrap",
                    border: this.submitFlag ? "1px solid #000" : "1px solid red",
                    boxSizing: 'border-box',
                    overflow: "hidden",
                    borderRadius: "4px",
                    height: "45px",
                    width: '5.26rem',
                    color: '#000'
                }}>
                    <img style={{textAlign: "left", height: "24px", verticalAlign: "middle", marginLeft: "20px"}}
                         src={this.props.icon}/>
                    <input placeholder={messages[localStorage['language']][this.props.text]} style={{
                        paddingLeft: "10px",
                        border: "0",
                        height: "100%",
                        width: "4.3rem",
                        fontSize: "12px",
                        color: '#000'
                    }} type="text" name={this.props.name} onChange={this.handleInputChange.bind(this, this.props.name)}
                           value={this.props.form[this.props.name]}/>
                </div>
            </div>
        </div>
    }
}

class ImageLine extends Component {
    constructor(props) {
        super(props);
        this.props.form['headImg'] = this.props.photo;
        this.error = {
            text: 'sure'
        }
        this.submitFlag = true
    }

    componentDidMount() {
        this.valid = () => {
            let $avatar = $("#avatar");
            if (($avatar.val() === undefined || $avatar.val() === '') && !this.props.form['headImg'].split(':').length) {
                this.submitFlag = false;

            } else if (this.props.form['headImg'].split(':').length) {
                if (this.props.form['headImg'].split(':')[0] === 'http') {
                    this.submitFlag = true;
                }
                else {
                    this.submitFlag = false;
                }
            }
            else {
                this.submitFlag = true
            }

            this.setState({
                submitFlag: this.submitFlag
            })
            if (this.submitFlag)
                return true
            else {
                this.error.text = 'avatar_image_is_null';
                this.setState(this.error);
                this.refs.Menu.open();
                setTimeout(() => {
                    this.refs.headImg.scrollIntoView()
                }, 300)
            }
            return false;
        }
    }

    triggerUpload = () => {
        $('#avatar').click();
    };
    doUpload = () => {
        let formData = new FormData($('#avatar-form')[0]);
        $.ajax({
            type: 'POST',
            url: Constant.url + 'fileUpload.do',
            data: formData,
            processData: false,
            contentType: false,
            beforeSend: () => {
                this.props.form['headImg'] = this.props.loading;
                this.setState(this.props.form);
            },
            success: (data) => {
                //TODO success callback
                if (data.error_no != '0') {
                    this.error.text = data.data;
                    this.setState(this.error);
                    this.refs.Menu.open();
                }
                else {
                    this.submitFlag = true;
                    this.setState({
                        submitFlag: this.submitFlag
                    })
                    this.props.form['headImg'] = data.data;
                    this.setState(this.props.form);
                }

            }
        })
    };

    render() {
        return <div ref='headImg'>
            <Menu ref="Menu" text={this.error.text}/>
            <div style={{textAlign: "left"}}>
                <form id="avatar-form"
                      style={{
                          border: this.submitFlag ? null : '1px solid red',
                          boxSizing: 'border-box',
                      }}>
                    <input id="avatar" onChange={this.doUpload} accept="image/*" type="file"
                           style={{position: "absolute", visibility: "none", width: "0", height: "0"}} name="avatar"/>
                    <div style={{
                        border: "1px solid #dadada",
                        boxSizing: 'border-box',
                        width: IsPcOrNot.get() ? "180px" : "110px",
                        height: IsPcOrNot.get() ? "180px" : "110px",
                        backgroundPosition: "center",
                        backgroundRepeat: "no-repeat",
                        backgroundImage: "url(" + this.props.ImageSrc + ")",
                        backgroundSize: IsPcOrNot.get() ? "90% auto" : "70% auto",
                        backgroundColor: "#f7f7f7",
                        display: "inline-block",
                        textAlign: "right",
                        padding: "5px",
                        verticalAlign: "top",
                        color: "#777"
                    }}>
                        <FormattedMessage id="example"/>
                    </div>
                    <div onClick={this.triggerUpload} style={{
                        borderTop: "1px solid #dadada",
                        borderRight: "1px solid #dadada",
                        borderBottom: "1px solid #dadada",
                        display: "inline-block",
                        width: IsPcOrNot.get() ? "180px" : "110px",
                        height: IsPcOrNot.get() ? "180px" : "110px",
                        borderLeft: "1px solid #dadada",
                        boxSizing: 'border-box',
                        backgroundPosition: "center",
                        backgroundImage: "url('" + this.props.form['headImg'] + "')",
                        backgroundRepeat: "no-repeat",
                        backgroundSize: this.props.form['headImg'] == this.props.photo ? "35% auto" : "contain",
                        verticalAlign: "top",
                        position: "relative",
                        cursor: "pointer"
                    }}>
                        {
                            this.props.form['headImg'] == this.props.photo ?
                                <div style={{
                                    textAlign: "center",
                                    width: "100%",
                                    position: "absolute",
                                    fontSize: "12px",
                                    color: "#777",
                                    bottom: "0",
                                    height: IsPcOrNot.get() ? "50px" : "20px"
                                }}>
                                    <FormattedMessage id="click_to_upload"/>
                                </div> : null
                        }
                    </div>
                </form>
                <div style={{fontSize: "13px", color: "#777", margin: '20px 0'}}>
                      <span style={{color: "red"}}>
                          <FormattedMessage id="red_face_check_tip"/>
                      </span>
                    <FormattedMessage id="face_check_tip"/>
                    <br/>
                    <br/>
                    <FormattedMessage id="file_support"/>
                </div>
            </div>
        </div>
    }
}

class RadioLine extends Component {
    constructor(props) {
        super(props);
        this.error = {
            text: "sure"
        }
        this.flag = false;
        this.submitFlag = true;
    }

    componentDidMount() {
        this.valid = () => {
            this.submitFlag = true;
            if (this.props.valid != undefined) {
                let valids = this.props.valid.split("||");
                for (let i = 0; i < valids.length; i++) {
                    if (valids[i] === 'notNull') {
                        if (this.props.form[this.props.name] === undefined || this.props.form[this.props.name].length === 0 || this.props.form[this.props.name] === '') {
                            setTimeout(() => {
                                this.refs.radioCheck.scrollIntoView();
                            }, 300);
                            this.error.text = "every_thing_is_notnull";
                            this.setState(this.error);
                            this.refs.Menu.open();
                            this.submitFlag = false;
                            break;
                        }
                    }
                }
            }
            this.setState({
                submitFlag: this.submitFlag
            })
            return this.submitFlag;
        }
    }

    radioChange = (key, val) => {
        this.props.form[key] = val;
        if (key === 'whetherUseAi' || key === 'whetherUseBaiduAi') {
            this.props.refresh();
        }
        if (key === 'whetherUseAi' && val == '0') {
            if (this.props.form['useAiSelect']) {
                delete this.props.form['useAiSelect']
            }
        }
        if (key === 'whetherUseBaiduAi' && val == '0') {
            if (this.props.form['useBaiduAiSelect']) {
                delete this.props.form['useBaiduAiSelect']
            }
        }
        this.submitFlag = true;
        this.setState({
            submitFlag: this.submitFlag
        })
        this.setState(this.props.form);
    }

    render() {
        return (
            <div style={{
                width: IsPcOrNot.get() ? '600px' : '5.26rem',
                height: 'auto',
                marginBottom: '20px',
                border: this.submitFlag ? null : '1px solid red',
                boxSizing: 'border-box'
            }} ref='radioCheck'>
                <Menu ref="Menu" text={this.error.text}/>
                {
                    this.props.text ? <div
                        style={{
                            marginBottom: '20px',
                            textAlign: 'left',
                            fontSize: '14px',
                            color: '#000'
                        }}>{messages[localStorage['language']][this.props.text]}</div> : null
                }
                <div style={{
                    display: 'flex',
                    alignItems: 'center',
                    flexWrap: 'wrap',
                }}>
                    {
                        this.props.data.map((item, key) => {
                            return (
                                <div key={key} onClick={this.radioChange.bind(this, this.props.name, item.value)}
                                     style={{
                                         display: 'flex',
                                         alignItems: 'center',
                                         marginRight: "20px",
                                         cursor: "pointer"
                                     }}>
                                    <div style={{
                                        width: '18px',
                                        height: '18px',
                                        borderRadius: '50%',
                                        border: '1px solid #000',
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'center',
                                        marginRight: '6px'
                                    }}>
                                        <div style={{
                                            width: '6px',
                                            height: '6px',
                                            backgroundColor: (this.props.form[this.props.name] == item.value) ? 'rgb(0, 143, 255)' : '',
                                            borderRadius: '50%'
                                        }}>
                                        </div>
                                    </div>
                                    <span>{item.text}</span>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}

export {InputLine, SelectLine, ImageLine, CheckBoxLine, RadioLine}


