"use strict";
import React, {Component, PropTypes} from 'react'
import './StudentApply.scss'
import IsPcOrNot from '../../../components/lib/IsPcOrNot.js'
import Constant from '../../../components/lib/Constant.js'
import Params from '../../../components/lib/QueryParams.js'
import en from '../../../locale/en';
import zh from '../../../locale/zh';
import $ from 'n-zepto'
import Header from '../../../components/Header/Header'
import Footer from '../../../components/Footer/Footer'
import Card from '../assets/card.png'
import Email from '../assets/email.png'
import Name from '../assets/name.png'
import PhoneIcon from '../assets/phone.png'
import Photo from '../assets/photo.png'
import School from '../assets/school.png'
import Subject from '../assets/subject.png'
import ImageSrc from '../assets/image.png'
import Loading from '../assets/loading.gif'

import {Menu, FormattedMessage} from '../../../components/lib/BaseComponent'
import { InputLine, SelectLine,ImageLine,CheckBoxLine,RadioLine} from '../../../components/lib/BaseForm'

let messages = {};
messages['en'] = en;
messages['zh-CN'] = zh;
messages['zh-cn'] = zh;



class StudentImageLine extends Component {
    constructor(props) {
        super(props);
        this.props.form['studentImg'] = Photo;
        this.error = {
            text: "sure"
        }
        this.submitFlag = true
    }

    componentDidMount() {
        this.valid = () => {
            let $avatar = $("#student");
            if ($avatar.val() == undefined || $avatar.val() == '') {
                this.error.text = "student_image_is_null";
                this.refs.Menu.open();
                this.refs.studentCard.scrollIntoView()
                this.submitFlag = false;
                this.setState(this.error);
                return false;
            } else {
                return true;
            }
        }
    }

    triggerUpload = () => {
        $('#student').click();
    };
    doUpload = () => {
        let formData = new FormData($('#student-form')[0]);
        $.ajax({
            type: 'POST',
            url: Constant.url + 'uploadStudentImg.do',
            data: formData,
            processData: false,
            contentType: false,
            beforeSend: () => {
                this.props.form['studentImg'] = Loading;
                this.setState(this.props.form);
            },
            success: (data) => {
                //TODO success callback
                if (data.error_no != '0') {
                    this.error.text = data.data;
                    this.setState(this.error);
                    this.refs.Menu.open();
                }
                else {
                    this.submitFlag = true;
                    this.props.form['studentImg'] = data.data;
                    this.setState(this.props.form);
                }
            }
        })
    };

    render() {
        return <div ref='studentCard' style={{border:this.submitFlag?'0':'1px solid red'}}>
            <Menu ref="Menu" text={this.error.text}/>
            <div style={{textAlign: "left"}}>
                <form id="student-form">
                    <input id="student" onChange={this.doUpload} accept="image/*" type="file"
                           style={{
                               position: "absolute",
                               visibility: "none",
                               width: "0",
                               height: "0",
                           }}
                           name="studentImg"/>
                    <div style={{
                        border: "1px solid #dadada",
                        width: IsPcOrNot.get() ? "180px" : "110px",
                        height: IsPcOrNot.get() ? "180px" : "110px",
                        backgroundPosition: "center",
                        backgroundRepeat: "no-repeat",
                        backgroundImage: "url(" + Card + ")",
                        backgroundSize: IsPcOrNot.get() ? "90% auto" : "70% auto",
                        backgroundColor: "#f7f7f7",
                        display: "inline-block",
                        textAlign: "right",
                        padding: "5px",
                        verticalAlign: "top",
                        color: "#777"
                    }}>
                        <FormattedMessage id="example"/>
                    </div>
                    <div onClick={this.triggerUpload} style={{
                        borderTop: "1px solid #dadada",
                        borderRight: "1px solid #dadada",
                        borderBottom: "1px solid #dadada",
                        display: "inline-block",
                        width: IsPcOrNot.get() ? "180px" : "110px",
                        height: IsPcOrNot.get() ? "180px" : "110px",
                        borderLeft: "1px solid #dadada",
                        backgroundPosition: "center",
                        backgroundImage: "url('" + this.props.form['studentImg'] + "')",
                        backgroundRepeat: "no-repeat",
                        backgroundSize: this.props.form['studentImg'] == Photo ? "35% auto" : "contain",
                        verticalAlign: "top",
                        position: "relative",
                        cursor: "pointer"
                    }}>
                        {
                            this.props.form['studentImg'] == Photo ?
                                <div style={{
                                    textAlign: "center",
                                    width: "100%",
                                    position: "absolute",
                                    fontSize: "12px",
                                    color: "#777",
                                    bottom: "0",
                                    height: IsPcOrNot.get() ? "50px" : "20px"
                                }}>
                                    <FormattedMessage id="click_to_upload"/>
                                </div> : null
                        }
                    </div>
                </form>
                <div style={{fontSize: "13px", color: "#777", marginTop: "20px"}}>
                    <FormattedMessage id="file_support"/>
                </div>
            </div>
        </div>
    }
}

class Title extends Component {
    render() {
        return <div style={IsPcOrNot.get() ? {
            fontSize: "24px",
            fontWeight: "600",
            margin: "60px 0 20px 0"
        } : {
            fontSize: "16px",
            fontWeight: "600",
            margin: "40px 20px 20px 20px"
        }}>
            <FormattedMessage id="student_check"/>
        </div>
    }
}

class Tip extends Component {
    render() {
        return <div style={IsPcOrNot.get() ? {
            fontSize: "12px",
            paddingBottom: "80px"
        } : {
            fontSize: "12px",
            margin: "0 20px"
        }}>
            <FormattedMessage id="student_check_tip"/>
        </div>
    }
}

class Form extends Component {
    constructor(props) {
        super(props);
        this.error = {
            text: 'sure'
        }
        this.form = {};
        this.form_names = ['headImg', 'studentImg', 'name', 'phone', 'email', 'sex', 'company', 'position', 'gradeSelect', 'whetherUseAi', 'useAiSelect', 'futureAiPlanSelect',
            'whetherFirstAttend', 'likeTribuneSelect', 'gainsResultSelect'];
        //15个字段
        this.sex = [{value: '男', text: messages[localStorage['language']]['sex1']}, {
            value: '女',
            text: messages[localStorage['language']]['sex2']
        }];
        this.noOrYes = [{value: '1', text: messages[localStorage['language']]['yes']}, {value: '0', text: messages[localStorage['language']]['no']}]

        this.gradeSelect = [];
        for(var i = 1; i < 7; i++){
            this.gradeSelect.push({
                'value': i + '',
                "text": messages[localStorage['language']]['gradeSelect'+i]
            })
        }
        this.useAi = false;
        this.suseAiSelectData = [];
        this.sfutureAiPlanSelectData = [];
        this.likeTribuneSelectData = [];
        this.gainsResultSelectData = [];
        for (let i = 1; i < 12; i++) {
            this.suseAiSelectData.push({
                'value': i + '',
                'text': 'useAiSelect' + i
            })
        }
        for (let i = 1; i < 14; i++) {
            this.sfutureAiPlanSelectData.push({
                'value': i + '',
                'text': 'useAiSelect' + i
            })
        }
        for (let i = 1; i < 10; i++) {
            this.likeTribuneSelectData.push({
                'value': i + '',
                'text': 'likeTribuneSelect' + i
            })
        }
        for (let i = 1; i < 7; i++) {
            this.gainsResultSelectData.push({
                'value': i + '',
                'text': 'gainsResultSelect' + i
            })
        }
    }

    componentDidMount() {

    };

    refresh() {
        this.setState(this.form);
    };

    submit = () => {
        let flag = true;
        for (let i = 0; i < this.form_names.length; i++) {
            if (this.refs[this.form_names[i]] !== undefined) {
                if (!this.refs[this.form_names[i]].valid()) {
                    flag = false;
                    break;
                }
            }
        }
        // console.log(this.form);
        if (flag) {
            this.form['type'] = '1';
            // console.log(form)
            $.post(Constant.url + 'ticketInfo.do', this.form, (data) => {
                if (data.error_no === '0') {
                    //TODO success call back*/
                    this.error.text = data.data;
                    this.setState(this.error);
                    this.refs.Menu.redirect(Constant.router_preffix + '/my_tickets');
                    this.refs.Menu.open();
                }
                else {
                    this.error.text = data.data;
                    this.setState(this.error);
                    this.refs.Menu.open();
                }
            });
        }
    };
    handDisplay = (flag) => {
        if (flag === '1') {
            this.useAi = true;
        }
        if (flag === '2') {
            this.useAi = false
        }
        this.setState({
            useAi: this.useAi
        });
        // console.log(this.useAi)
    };

    render() {
        return <div>
            <Title/>
            <Tip/>
            <Menu ref="Menu" text={this.error.text}/>
            <table className={IsPcOrNot.get() ? "form-table" : "m-table"}>
                <tbody>
                <tr>
                    <td>
                        <FormattedMessage id="upload_student_image"/>
                    </td>
                    <td>
                        <StudentImageLine form={this.form} ref={this.form_names[0]} text="student_image" name={this.form_names[0]}
                                          valid="notNull"/>
                    </td>
                </tr>
                <tr>
                    <td>
                        <FormattedMessage id="upload_avatar"/>
                    </td>
                    <td>

                        <ImageLine form={this.form} photo={Photo} ImageSrc={ImageSrc} loading={Loading} ref={this.form_names[1]} text="student_image" name={this.form_names[1]}
                                   valid="notNull"/>
                    </td>
                </tr>
                <tr>
                    <td>
                        <FormattedMessage id="base_info"/>
                    </td>
                    <td>
                        <InputLine form={this.form} ref={this.form_names[2]} icon={Name} text="name" name={this.form_names[2]}
                                  valid="notNull||name"/>
                        <InputLine form={this.form} ref={this.form_names[3]} icon={PhoneIcon} text="phone" name={this.form_names[3]}
                                  valid="notNull"/>
                        <InputLine form={this.form} ref={this.form_names[4]} icon={Email} text="email" name={this.form_names[4]}
                                  valid="notNull"/>
                        <RadioLine form={this.form} ref={this.form_names[5]} text="sex" data={this.sex} name={this.form_names[5]} valid="notNull"/>
                    </td>
                </tr>
                <tr>
                    <td>
                        <FormattedMessage id="school_info"/>
                    </td>
                    <td>
                        <InputLine form={this.form} ref={this.form_names[6]} icon={School} text="school" name={this.form_names[6]}
                                  valid="notNull"/>
                        <InputLine form={this.form} ref={this.form_names[7]} icon={Subject} text="position" name={this.form_names[7]}
                                  valid="notNull"/>
                        <SelectLine form={this.form} ref={this.form_names[8]} text="gradeSelect" data={this.gradeSelect} name={this.form_names[8]}
                                    valid="notNull"/>
                    </td>
                </tr>
                <tr>
                    <td>
                        <FormattedMessage id="use_ai_info"/>
                    </td>
                    <td>
                        <RadioLine form={this.form} ref={this.form_names[9]} refresh={this.refresh.bind(this)} text="swhetherUseAi" data={this.noOrYes}
                                    name={this.form_names[9]} handDisplay={this.handDisplay.bind(this)}
                                    valid="notNull"/>
                        {
                            this.form['whetherUseAi'] === '1' ?
                                <CheckBoxLine form={this.form} ref={this.form_names[10]} text="suseAiSelect"  data={this.suseAiSelectData} name={this.form_names[10]}
                                            valid="notNull"/> : null
                        }
                        <CheckBoxLine form={this.form} ref={this.form_names[11]} text="sfutureAiPlanSelect"  data={this.sfutureAiPlanSelectData} name={this.form_names[11]}
                                    valid="notNull"/>
                    </td>
                </tr>
                <tr>
                    <td>
                        <FormattedMessage id="join_info"/>
                    </td>
                    <td>
                        <RadioLine form={this.form} ref={this.form_names[12]} text="whetherFirstAttend" name={this.form_names[12]} data={this.noOrYes}
                                    valid="notNull"/>
                        <CheckBoxLine form={this.form} ref={this.form_names[13]} text="likeTribuneSelect"  data={this.likeTribuneSelectData} name={this.form_names[13]}
                                    valid="notNull"/>
                        <CheckBoxLine form={this.form} ref={this.form_names[14]} text="gainsResultSelect"  data={this.gainsResultSelectData} name={this.form_names[14]}
                                    valid="notNull"/>
                        <button type="submit" className="base_type_btn"
                                style={{padding: "15px 100px", borderRadius: "3px", margin: "40px 0 80px 0"}}
                                onClick={this.submit}>
                            <FormattedMessage id="submit"/>
                        </button>
                        {
                            IsPcOrNot.get() ? <div style={{textAlign: 'left'}}>
                                <div style={{padding: "20px 0 20px 0", color: "red", fontSize: "18px"}}>
                                    <FormattedMessage id="tips"/>
                                </div>
                                <div style={{color: "#777"}}>
                                    <FormattedMessage id="student_apply_tip1"/>
                                </div>
                                <div style={{color: "#777"}}>
                                    <FormattedMessage id="student_apply_tip2"/>
                                </div>
                                <div style={{color: "#777"}}>
                                    <FormattedMessage id="student_apply_tip3"/>
                                </div>
                            </div> : null
                        }
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
    }
}

class Phone extends Component {
    render() {
        return <div style={{backgroundColor: "#fff"}}>
            <Header/>
            <Form/>
            <Footer/>
        </div>
    }
}

class PC extends Component {
    render() {
        return <div style={{backgroundColor: "#fff"}}>
            <Header/>
            <div style={{margin: "0 auto", display: "block", position: "relative", width: "1200px"}}>
                <div style={{
                    position: "relative",
                    width: "100%",
                    whiteSpace: "nowrap",
                    padding: "40px 0",
                    fontSize: "18px"
                }}>
                    <div style={{display: "inline-block", width: "33.33%"}}>
                        <FormattedMessage id="student_ticket"/>
                    </div>
                    <div style={{display: "inline-block", width: "33.33%", textAlign: "center"}}>
                        <FormattedMessage id="one_ticket"/>
                    </div>
                    <div style={{display: "inline-block", width: "33.33%", textAlign: "right", color: "#467cf5"}}>
                        <FormattedMessage id="total_student_price"/>
                    </div>
                </div>
                <Form/>
            </div>
            <Footer/>
        </div>
    }
}

export class StudentApply extends Component {
    constructor(props) {
        super(props);
    }

    changeLanguage = () => {
        if (localStorage['language'] == 'en') {
            localStorage['language'] = 'zh-CN';
        }
        else {
            localStorage['language'] = 'en';
        }
        location.href = location.href;
    };
    getLoginStatus = () => {
        let logined = false;
        $.get(Constant.url + 'hasLoginBaidu.do', (data) => {
            if (data.error_no == '0') {
                logined = false;
            }
            else {
                logined = true;
            }
        })
    }

    componentDidMount() {
        this.getLoginStatus();

    }

    login = () => {
        IsPcOrNot.get() ? window.instance.show() : window.instance.render($("#login")[0]);
    };

    render() {
        return <div>
            {
                IsPcOrNot.get() ?
                    <PC/> : <Phone/>
            }
        </div>
    }
}

export default StudentApply;


